/* global jQuery */

(function ($) {
	// put JavaScript here

	"use strict";

	var tab = $(".js-txt-header");

	if( tab.length && !tab.parents(".q-accordion-item").hasClass("active") ){

		$(".js-txt-header").mouseenter(function() {

			let $this = $(this);
			let $accordion = $this.parents(".js-accordion-items");
			let $screnshots = $accordion.next(".q-accordion-images");
			let $imageData = $this.data("image");

			if( $accordion.hasClass("accordion-init") ){
				$accordion.removeClass("accordion-init");
			}

			$this.parents(".q-accordion-items").find(".q-accordion-item").removeClass("active");
			$this.parents(".q-accordion-item").addClass("active");
			$(this).parents(".q-accordion-items").find(".q-accordion-item:not(.active) .js-txt-content").hide();
			$this.next().slideDown(350);

			/* $screnshots.find(".js-img-screenshot").fadeOut("fast", function(){
				$("."+ $imageData).fadeIn("fast");
			});*/

			$screnshots.find(".js-img-screenshot").hide(0, function(){
				$("."+ $imageData).show(0);
			});
		});

	}



}(jQuery));